import LastSalesClient, { Sale } from '@/components/lastsales/LastSalesClient'

// Página del servidor: prepara los datos (mock) y pasa al componente cliente.
// Reemplaza la sección "fetchSales" con una llamada a tu API real (OpenSea/Covalent/Moralis/etc.)
// por ejemplo:
// const res = await fetch(process.env.OPENSEA_SALES_ENDPOINT!, { headers: { 'X-API-KEY': process.env.OPENSEA_KEY } })
// const sales = await res.json()

async function fetchSales(): Promise<Sale[]> {
	// Mock: usa esto hasta que conectes una API real
	return [
		{
			id: '1',
			collection: 'PhantomBeasts',
			tokenId: '1024',
			price: 2.5,
			currency: 'ETH',
			date: new Date().toISOString(),
			traits: [
				{ trait_type: 'Rarity', value: 'Legendary' },
				{ trait_type: 'Color', value: 'Crimson' },
			],
		},
		{
			id: '2',
			collection: 'CrystalCats',
			tokenId: '7',
			price: 0.45,
			currency: 'ETH',
			date: new Date(Date.now() - 1000 * 60 * 60).toISOString(),
			traits: [
				{ trait_type: 'Background', value: 'Space' },
				{ trait_type: 'Eyes', value: 'Laser' },
			],
		},
		{
			id: '3',
			collection: 'PhantomBeasts',
			tokenId: '2048',
			price: 1.1,
			currency: 'ETH',
			date: new Date(Date.now() - 1000 * 60 * 60 * 5).toISOString(),
			traits: [
				{ trait_type: 'Rarity', value: 'Rare' },
				{ trait_type: 'Color', value: 'Blue' },
			],
		},
	]
}

export default async function Page() {
	const sales = await fetchSales()

	return (
		<main className="container mx-auto py-8">
			<LastSalesClient sales={sales} />
		</main>
	)
}

